	module.exports = {"data":{"post":{"html":"<p><a href=\"http://en.wikipedia.org/wiki/Michael_Glos\">Michael Glos</a> is Germany's Economics and Technology Minister. He doesn't really understand the internet, but then <a href=\"http://blog.handelsblatt.de/indiskretion/eintrag.php?id=1167\">he doesn't need to</a>:</p>\n<blockquote><p>\n  Thankfully I have people who operate the internet for me<br />\n  <em>(Ich habe Gott sei Dank Leute, die für mich das Internet bedienen)</em>\n</p></blockquote>\n<p>I guess that means his secretary prints out his email for him.</p>\n<p>He actually spends most of his time protecting Germany's car industry. He doubt that he understands cars either, but he has people who operate them for him. (via <a href=\"http://www.ringfahndung.de/archives/reine_machtkritik/michael_glos_vorzimmerdame_20_susser_referent_20.html\">Ringfahndung</a>)</p>","fields":{"title":"Internet Operator","date":"19 Mar 2007","url":"https://papascott.de/2007-03-19-internet-operator/"}}},"pathContext":{"slug":"/2007-03-19-internet-operator/","prev":{"fields":{"title":"Dante's CeBIT","date":"19 Mar 2007","slug":"/2007-03-19-dantes-cebit/"}},"next":{"fields":{"title":"Samsung Q35 Red","date":"19 Mar 2007","slug":"/2007-03-19-samsung-q35-red/"}}}}

